import React from 'react';
import { graphql, PageProps } from 'gatsby';

import AppContainer from '../layouts/AppContainer';
import ContentContainer from '../components/ContentContainer';
import BlogArchive, { PostType } from '../components/page/BlogArchive';

export default function TagTemplate({ data }: PageProps<Queries.TagTemplateQuery>) {
  return (
    <AppContainer title={`标签：${false}`}>
      <ContentContainer>
        <BlogArchive posts={data.allMarkdownRemark.edges as unknown as Array<PostType>} />
      </ContentContainer>
    </AppContainer>
  );
}

export const query = graphql`
  query TagTemplate($tag: String!) {
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }, filter: { frontmatter: { tags: { eq: $tag }, publish: { eq: true } } }) {
      totalCount
      edges {
        node {
          id
          excerpt
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
            cover
            tags
          }
          parent {
            ... on File {
              relativePath
            }
          }
        }
      }
    }
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`;
